import axios from 'axios';
import { createAsyncThunk } from '@reduxjs/toolkit';
import toast from 'react-hot-toast';

axios.defaults.baseURL = 'https://connections-api.herokuapp.com';

const token = {
  set(token) {
    axios.defaults.headers.common.Authorization = `Bearer ${token}`;
  },
  unset() {
    axios.defaults.headers.common.Authorization = '';
  },
};

const register = createAsyncThunk('auth/register', async (credentials, { rejectWithValue }) => {
  try {
    const { data } = await axios.post('/users/signup', credentials);
    token.set(data.token);
    return data;
  } catch (error) {
    toast('Something went wrong');
    return rejectWithValue(error.message);  }
});

const logIn = createAsyncThunk('auth/login', async (credentials, { rejectWithValue }) => {
  try {
    const { data } = await axios.post('/users/login', credentials);
    token.set(data.token);
    return data;
  } catch (error) {
    toast('Something went wrong');
    return rejectWithValue(error.message); 
  }
});

const logOut = createAsyncThunk('auth/logout', async (_, { rejectWithValue }) => {
  try {
    await axios.post('/users/logout');
    token.unset();
  } catch (error) {
    toast('Something went wrong');
    return rejectWithValue(error.message); 
  }
});
/*
 * GET @ /users/current
 * headers:
 *    Authorization: Bearer token
 *
 * 1. –ó–∞–±–∏—Ä–∞–µ–º —Ç–æ–∫–µ–Ω –∏–∑ —Å—Ç–µ–π—Ç–∞ —á–µ—Ä–µ–∑ getState()
 * 2. –ï—Å–ª–∏ —Ç–æ–∫–µ–Ω–∞ –Ω–µ—Ç, –≤—ã—Ö–æ–¥–∏–º –Ω–µ –≤—ã–ø–æ–ª–Ω—è—è –Ω–∏–∫–∞–∫–∏—Ö –æ–ø–µ—Ä–∞—Ü–∏–π
 * 3. –ï—Å–ª–∏ —Ç–æ–∫–µ–Ω –µ—Å—Ç—å, –¥–æ–±–∞–≤–ª—è–µ—Ç –µ–≥–æ –≤ HTTP-–∑–∞–≥–æ–ª–æ–≤–æ–∫ –∏ –≤—ã–ø–æ–ª—è–Ω–µ–º –æ–ø–µ—Ä–∞—Ü–∏—é
 */
const fetchCurrentUser = createAsyncThunk(
  'auth/refresh',
  async (_, thunkAPI) => {
    const state = thunkAPI.getState();
    const persistedToken = state.auth.token;

    if (persistedToken === null) {
      return thunkAPI.rejectWithValue();
    }

    token.set(persistedToken);
    try {
      const { data } = await axios.get('/users/current');
      return data;
    } catch (error) {
      toast('Something went wrong');
      return thunkAPI.rejectWithValue(error.message); 
    }
  },
);

const authOperations = {
  register,
  logOut,
  logIn,
  fetchCurrentUser,
};
export default authOperations;
